@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&display=swap);
.app__header{display:flex;justify-content:space-between}.app__title{font-weight:bold;font-size:28px;line-height:37px;color:#fff}.app__title span{color:#2fc7cf}.app__menu{color:#fff}.app__menu ul{display:flex;justify-content:space-between;font-weight:bold;font-size:24px;line-height:32px}.app__menu ul li{margin:0 8px}.app__menu ul li a:hover{color:#2fc7cf}
.container{margin:auto auto;display:flex;flex-direction:column;justify-content:center;align-items:center}.tittle{margin-bottom:40px;color:#44b9ff;letter-spacing:4px;text-transform:uppercase}.square-container{list-style-type:none;display:flex;position:relative}.square{margin:4px;width:30px;height:30px;border-radius:7px;-webkit-animation:rotating 2s ease infinite;animation:rotating 2s ease infinite}.square1{background:#736efe;-webkit-animation-delay:.2s;animation-delay:.2s}.square2{background:#5999ff;-webkit-animation-delay:.4s;animation-delay:.4s}.square3{background:#44b9ff;-webkit-animation-delay:.6s;animation-delay:.6s}.square4{background:#31d8ff;-webkit-animation-delay:.8s;animation-delay:.8s}.square5{background:#18ffff;-webkit-animation-delay:1s;animation-delay:1s}@-webkit-keyframes rotating{0%{-webkit-transform:rotate(0) scale(1);transform:rotate(0) scale(1)}50%{-webkit-transform:rotate(90deg) scale(0.6);transform:rotate(90deg) scale(0.6)}100%{-webkit-transform:rotate(90deg) scale(1);transform:rotate(90deg) scale(1)}}@keyframes rotating{0%{-webkit-transform:rotate(0) scale(1);transform:rotate(0) scale(1)}50%{-webkit-transform:rotate(90deg) scale(0.6);transform:rotate(90deg) scale(0.6)}100%{-webkit-transform:rotate(90deg) scale(1);transform:rotate(90deg) scale(1)}}
.button{min-width:101px;color:#fff;text-align:center;text-transform:uppercase;font-size:14px;letter-spacing:3px;transition:.3s transform;border:none;background-color:transparent;cursor:pointer}.button__long{display:block;width:170px;margin:45px auto 0 auto}.button .inner{position:relative;background-color:#2fc7cf;line-height:18px;padding:0 18px;transition:none}.button .inner::before{border-color:#2fc7cf transparent;border-style:solid;border-width:0 0 10px 10px;content:"";display:block;position:absolute;left:0;top:-10px;transition:none}.button .inner::after{border-color:#2fc7cf transparent;border-style:solid;border-width:0 0 10px 10px;content:"";display:block;position:absolute;right:0;bottom:-10px;-webkit-transform:rotate(180deg);transform:rotate(180deg);transition:none}.button__main:hover,.button__secondary:hover{color:#fff}.button__secondary .inner{background-color:#243b55}.button__secondary .inner::before{border-color:#243b55 transparent;transition:none}.button__secondary .inner::after{border-color:#243b55 transparent;transition:none}.button::before{content:"";display:block;height:10px;margin-left:10px;transition:none}.button::after{content:"";display:block;height:10px;margin-right:10px;transition:none}.button.button__main::before,.button.button__main::after{background-color:#2fc7cf}.button.button__secondary::before,.button.button__secondary::after{background-color:#243b55}.button:hover{-webkit-transform:translateY(-5px);transform:translateY(-5px)}.button:disabled{-webkit-filter:grayscale(0.8);filter:grayscale(0.8)}.button__moving{position:relative;min-width:101px;padding:10px 20px;color:#03e9f4;font-size:14px;text-decoration:none;text-transform:uppercase;overflow:hidden;transition:.5s;letter-spacing:3px;cursor:pointer;background-color:transparent;text-align:center;transition:.3s transform;border:none}.button__moving:hover{background:#03e9f4;color:#fff;border-radius:5px;box-shadow:0 0 5px #03e9f4,0 0 25px #03e9f4,0 0 50px #03e9f4,0 0 100px #03e9f4}.button__moving 