px;margin:0 auto;padding:50px 0 50px 0;position:relative}.app .bg-decoration{position:absolute;right:-174px;bottom:-70px;z-index:-1}main{margin-top:50px;position:relative}.pulse{-webkit-animation:pulse 1.5s ease-in-out .5s infinite;animation:pulse 1.5s ease-in-out .5s infinite}@-webkit-keyframes pulse{0%{opacity:1}50%{opacity:.4}100%{opacity:1}}@keyframes pulse{0%{opacity:1}50%{opacity:.4}100%{opacity:1}}
